/**
 * OAuth Store for React State Management
 * Manages OAuth authentication state using Zustand
 */

import { create } from 'zustand';
import { persist } from 'zustand/middleware';
import { useEffect } from 'react';
import oauthService, { OAuthStatus, AuthenticatedChannelData } from '../services/oauth';

interface OAuthState {
  // Authentication state
  isAuthenticated: boolean;
  status: OAuthStatus | null;
  isLoading: boolean;
  error: string | null;
  isInitialized: boolean; // Track if we've checked status at least once
  
  // User info
  userId: string;
  
  // OAuth flow state
  isAuthenticating: boolean;
  
  // Authenticated data
  authenticatedChannelData: AuthenticatedChannelData | null;
  
  // Actions
  setUserId: (userId: string) => void;
  checkStatus: () => Promise<void>;
  initiateOAuth: (returnUrl?: string) => Promise<void>;
  handleCallback: () => { success: boolean; error?: string };
  refreshToken: () => Promise<boolean>;
  revokeToken: () => Promise<boolean>;
  getChannelData: (channelId: string) => Promise<AuthenticatedChannelData | null>;
  clearError: () => void;
  forceRefreshStatus: () => Promise<void>;
  resetAuthentication: () => void;
}

export const useOAuthStore = create<OAuthState>()(
  persist(
    (set, get) => ({
      // Initial state
      isAuthenticated: false,
      status: null,
      isLoading: false,
      error: null,
      isInitialized: false,
      userId: localStorage.getItem('creatormate_user_id') || 'default_user',
      isAuthenticating: false,
      authenticatedChannelData: null,

      // Set user ID
      setUserId: (userId: string) => {
        localStorage.setItem('creatormate_user_id', userId);
        set({ userId });
      },

      // Check OAuth status
      checkStatus: async () => {
        const { userId } = get();
        // Clear any previous errors when checking status
        set({ isLoading: true, error: null });

        try {
          const status = await oauthService.checkOAuthStatus(userId);
          
          // Don't auto-refresh on every page load - only when user explicitly needs it
          // The connection is working fine even with an old timestamp
          
          set({
            status,
            isAuthenticated: status.authenticated,
            isLoading: false,
            error: null,
            isInitialized: true
          });
        } catch (error) {
          // Always update authentication status based on the response
          // Even if there's an error, we got a response from our service
          set({
            isLoading: false,
            error: error instanceof Error ? error.message : 'Failed to check OAuth status',
            isInitialized: true,
            // If we get an error response from our service, it means we're not authenticated
            isAuthenticated: false,
            status: null
          });
        }
      },

      // Initiate OAuth flow
      initiateOAuth: async (returnUrl?: string) => {
        const { userId } = get();
        set({ isAuthenticating: true, error: null });

        try {
          const authUrl = await oauthService.initiateOAuth({
            user_id: userId,
            return_url: returnUrl
          });

          // Redirect to Google OAuth
          window.location.href = authUrl;
        } catch (error) {
          set({
            isAuthenticating: false,
            error: error instanceof Error ? error.message : 'Failed to initiate OAuth'
          });
        }
      },

      // Handle OAuth callback
      handleCallback: () => {
        const result = oauthService.handleOAuthCallback();
        
        if (result.success) {
          set({ 
            isAuthenticating: false, 
            error: null 
          });
          
          // Check status after successful callback
          get().checkStatus();
          
          return { success: true, userId: result.userId };
        } else if (result.error) {
          set({ 
            isAuthenticating: false, 
            error: result.error 
          });
          
          return { success: false, error: result.error };
        }
        
        return { success: false };
      },

      // Refresh token
      refreshToken: async () => {
        const { userId } = get();
        set({ isLoading: true, error: null });

        try {
          const success = await oauthService.refreshToken(userId);
          
          if (success) {
            // Refresh status after token refresh
            await get().checkStatus();
            return true;
          } else {
            set({ 
              isLoading: false,
              error: 'Failed to refresh token'
            });
            return false;
          }
        } catch (error) {
          set({
            isLoading: false,
            error: error instanceof Error ? error.message : 'Failed to refresh token'
          });
          return false;
        }
      },

      // Revoke token
      revokeToken: async () => {
        const { userId } = get();
        set({ isLoading: true, error: null });

        try {
          const success = await oauthService.revokeToken(userId);
          
          if (success) {
            set({
              isAuthenticated: false,
              status: null,
              authenticatedChannelData: null,
              isLoading: false,
              error: null
            });
            return true;
          } else {
            set({ 
              isLoading: false,
              error: 'Failed to revoke token'
            });
            return false;
          }
        } catch (error) {
          set({
            isLoading: false,
            error: error instanceof Error ? error.message : 'Failed to revoke token'
          });
          return false;
        }
      },

      // Get authenticated channel data
      getChannelData: async (channelId: string) => {
        const { userId, isAuthenticated } = get();
        
        if (!isAuthenticated) {
          set({ error: 'Not authenticated. Please connect your YouTube account first.' });
          return null;
        }

        set({ isLoading: true, error: null });

        try {
          const data = await oauthService.getAuthenticatedChannelData(channelId, userId);
          set({
            authenticatedChannelData: data,
            isLoading: false,
            error: null
          });
          return data;
        } catch (error) {
          set({
            isLoading: false,
            error: error instanceof Error ? error.message : 'Failed to get channel data'
          });
          return null;
        }
      },

      // Clear error
      clearError: () => {
        set({ error: null });
      },

      // Force refresh status (clears any stuck state)
      forceRefreshStatus: async () => {
        // Reset state before checking
        set({ 
          isLoading: true, 
          error: null,
          isInitialized: false 
        });
        
        // Small delay to ensure UI updates
        await new Promise(resolve => setTimeout(resolve, 100));
        
        // Now check status fresh
        await get().checkStatus();
      },

      // Reset authentication state
      resetAuthentication: () => {
        oauthService.resetAuthenticationState();
        set({
          isAuthenticated: false,
          status: null,
          isAuthenticating: false,
          authenticatedChannelData: null,
          error: null
        });
      }
    }),
    {
      name: 'oauth-store',
      partialize: (state) => ({
        userId: state.userId,
        // Don't persist sensitive auth data - it will be refreshed on app load
      })
    }
  )
);

// Custom hook for OAuth status checking
export const useOAuthStatus = () => {
  const store = useOAuthStore();
  
  // Auto-check status on mount and periodically
  useEffect(() => {
    // Check status on mount
    store.checkStatus();
    
    // Set up periodic status checking (every 15 minutes, only if authenticated)
    const interval = setInterval(() => {
      if (store.isAuthenticated) {
        store.checkStatus();
      }
    }, 15 * 60 * 1000);
    
    return () => clearInterval(interval);
  }, [store.checkStatus, store.isAuthenticated]);
  
  return store;
};

export default useOAuthStore;