/**
 * OAuth Service for YouTube Authentication
 * Handles OAuth 2.0 flow integration with React
 */

export interface OAuthStatus {
  authenticated: boolean;
  user_id: string;
  expires_at?: string;
  expires_in_seconds?: number;
  scopes?: string[];
  needs_refresh?: boolean;
  message?: string;
}

export interface OAuthInitRequest {
  user_id: string;
  return_url?: string;
}

export interface OAuthInitResponse {
  authorization_url: string;
  state: string;
  user_id: string;
  return_url?: string;
  message: string;
}

export interface AuthenticatedChannelData {
  channel_data: {
    basic_info: {
      channel_id: string;
      title: string;
      subscriber_count: number;
      video_count: number;
      view_count: number;
      upload_frequency: string;
    };
    engagement_metrics: {
      avg_views_last_30: number;
      avg_engagement_last_30: number;
      growth_rate: number;
    };
    recent_videos: Array<{
      video_id: string;
      title: string;
      view_count: number;
      like_count: number;
      comment_count: number;
      published_at: string;
      engagement_rate: number;
    }>;
  };
  analytics_data: any;
  oauth_authenticated: boolean;
  status: string;
}

class OAuthService {
  private baseUrl = ''; // Use relative URLs to avoid CORS and routing issues
  private isAuthenticating = false;
  private defaultTimeout = 10000; // 10 seconds default timeout
  private statusCheckTimeout = 5000; // 5 seconds for status checks

  /**
   * Create fetch with timeout and retry logic
   */
  private async fetchWithTimeout(url: string, options: RequestInit = {}, timeout: number = this.defaultTimeout, retries: number = 2): Promise<Response> {
    let lastError: Error;
    
    for (let attempt = 0; attempt <= retries; attempt++) {
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), timeout);

      try {
        const response = await fetch(url, {
          ...options,
          signal: controller.signal
        });
        clearTimeout(timeoutId);
        
        // If response is successful, return it
        if (response.ok) {
          return response;
        }
        
        // For non-2xx responses, don't retry unless it's a server error (5xx)
        if (response.status < 500 || attempt === retries) {
          return response;
        }
        
        // Wait before retrying (exponential backoff)
        await this.delay(Math.pow(2, attempt) * 1000);
        
      } catch (error) {
        clearTimeout(timeoutId);
        lastError = error instanceof Error ? error : new Error('Unknown error');
        
        if (lastError.name === 'AbortError') {
          lastError = new Error(`Request timed out after ${timeout}ms`);
        }
        
        // Don't retry on timeout or network errors if this is the last attempt
        if (attempt === retries) {
          throw lastError;
        }
        
        // Wait before retrying (exponential backoff)
        await this.delay(Math.pow(2, attempt) * 1000);
      }
    }
    
    throw lastError!;
  }

  /**
   * Utility function for delays
   */
  private delay(ms: number): Promise<void> {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  /**
   * Check current OAuth authentication status
   */
  async checkOAuthStatus(userId: string): Promise<OAuthStatus> {
    try {
      const response = await this.fetchWithTimeout(
        `${this.baseUrl}/auth/status/${userId}`,
        {},
        this.statusCheckTimeout
      );
      
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }
      const data = await response.json();
      
      // Transform backend response to match frontend interface
      const transformedStatus = {
        authenticated: data.authenticated,
        user_id: data.user_id,
        expires_at: data.expires_at,
        expires_in_seconds: data.expires_in_seconds,
        scopes: data.scopes,
        needs_refresh: data.needs_refresh,
        message: data.message
      };
      
      return transformedStatus;
    } catch (error) {
      console.error('Error checking OAuth status:', error);
      
      // Provide specific error messages for different scenarios
      let errorMessage = 'Failed to check authentication status';
      if (error instanceof Error) {
        if (error.message.includes('timed out')) {
          errorMessage = 'Connection timeout - server may be slow or unavailable';
        } else if (error.message.includes('Failed to fetch')) {
          errorMessage = 'Network error - check your internet connection';
        } else {
          errorMessage = error.message;
        }
      }
      
      return {
        authenticated: false,
        user_id: userId,
        message: errorMessage
      };
    }
  }

  /**
   * Initiate OAuth authorization flow
   */
  async initiateOAuth(request: OAuthInitRequest): Promise<string> {
    if (this.isAuthenticating) {
      throw new Error('OAuth flow already in progress');
    }

    this.isAuthenticating = true;

    try {
      const response = await this.fetchWithTimeout(`${this.baseUrl}/auth/initiate`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          user_id: request.user_id,
          return_url: request.return_url || window.location.href
        })
      });

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      const data: OAuthInitResponse = await response.json();

      if (data.authorization_url) {
        // Store state for validation
        localStorage.setItem('oauth_state', data.state);
        localStorage.setItem('oauth_return_url', request.return_url || window.location.href);
        
        return data.authorization_url;
      } else {
        throw new Error(data.message || 'Failed to initiate OAuth');
      }
    } catch (error) {
      this.isAuthenticating = false;
      throw error;
    }
  }

  /**
   * Handle OAuth callback parameters
   */
  handleOAuthCallback(): { success: boolean; error?: string; userId?: string } {
    const urlParams = new URLSearchParams(window.location.search);
    const oauthSuccess = urlParams.get('oauth_success');
    const oauthError = urlParams.get('oauth_error');
    const userId = urlParams.get('user_id');

    if (oauthSuccess === 'true') {
      // Clean up URL parameters
      window.history.replaceState({}, document.title, window.location.pathname);
      this.isAuthenticating = false;
      return { success: true, userId: userId || undefined };
    } else if (oauthError) {
      // Clean up URL parameters
      window.history.replaceState({}, document.title, window.location.pathname);
      this.isAuthenticating = false;
      
      let errorMessage = 'YouTube authentication failed. ';
      
      switch (oauthError) {
        case 'access_denied':
          errorMessage += 'Access was denied by user.';
          break;
        case 'invalid_request':
          errorMessage += 'Invalid request parameters.';
          break;
        case 'server_error':
          errorMessage += 'Server error occurred.';
          break;
        case 'callback_failed':
          errorMessage += 'Authentication callback failed.';
          break;
        case 'missing_parameters':
          errorMessage += 'Missing required parameters.';
          break;
        default:
          errorMessage += 'Please try again.';
      }
      
      return { success: false, error: errorMessage };
    }

    return { success: false };
  }

  /**
   * Refresh OAuth token
   */
  async refreshToken(userId: string): Promise<boolean> {
    try {
      const response = await this.fetchWithTimeout(`${this.baseUrl}/auth/refresh/${userId}`, {
        method: 'POST'
      });

      if (response.ok) {
        return true;
      } else {
        const data = await response.json().catch(() => ({}));
        throw new Error(data.detail || data.message || 'Failed to refresh token');
      }
    } catch (error) {
      console.error('Error refreshing token:', error);
      return false;
    }
  }

  /**
   * Revoke OAuth token
   */
  async revokeToken(userId: string): Promise<boolean> {
    try {
      const response = await this.fetchWithTimeout(`${this.baseUrl}/auth/revoke/${userId}`, {
        method: 'DELETE'
      });

      if (response.ok) {
        return true;
      } else {
        const data = await response.json().catch(() => ({}));
        throw new Error(data.detail || data.message || 'Failed to revoke token');
      }
    } catch (error) {
      console.error('Error revoking token:', error);
      return false;
    }
  }

  /**
   * Get authenticated YouTube channel data
   */
  async getAuthenticatedChannelData(channelId: string, userId: string): Promise<AuthenticatedChannelData> {
    try {
      const response = await this.fetchWithTimeout(`${this.baseUrl}/api/youtube/analytics/authenticated`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          channel_id: channelId,
          user_id: userId,
          include_videos: true,
          video_count: 10,
          analysis_type: 'comprehensive'
        })
      }, 15000); // Longer timeout for data fetching

      if (response.ok) {
        return await response.json();
      } else {
        const data = await response.json();
        throw new Error(data.detail || 'Failed to get authenticated channel data');
      }
    } catch (error) {
      console.error('Error getting authenticated channel data:', error);
      throw error;
    }
  }

  /**
   * Check OAuth system health
   */
  async checkOAuthHealth(): Promise<any> {
    try {
      const response = await this.fetchWithTimeout(`${this.baseUrl}/auth/health`, {}, 3000); // Quick health check
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }
      return await response.json();
    } catch (error) {
      console.error('Error checking OAuth health:', error);
      
      let errorMessage = 'Unknown error';
      if (error instanceof Error) {
        if (error.message.includes('timed out')) {
          errorMessage = 'Server health check timed out';
        } else {
          errorMessage = error.message;
        }
      }
      
      return { status: 'unhealthy', error: errorMessage };
    }
  }

  /**
   * Check if currently authenticating
   */
  getIsAuthenticating(): boolean {
    return this.isAuthenticating;
  }

  /**
   * Reset authentication state
   */
  resetAuthenticationState(): void {
    this.isAuthenticating = false;
  }
}

// Export singleton instance
export const oauthService = new OAuthService();
export default oauthService;