import { useState, useEffect } from 'react';
import { useForm, useFieldArray } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { X, Trash2, Calendar } from 'lucide-react';
import { ContentCard, CardStatus, Pillar } from '@/types/contentStudio';
import { cardFormSchema, CardFormData } from '@/lib/validationSchemas';
import { usePillars } from '@/hooks/usePillars';

interface CardModalProps {
    isOpen: boolean;
    onClose: () => void;
    onSubmit: (data: CardFormData) => Promise<boolean>;
    card?: ContentCard | null;
    defaultStatus?: CardStatus;
}

const statusOptions: { value: CardStatus; label: string; color: string }[] = [
    { value: 'ideas', label: 'Ideas', color: 'bg-purple-500' },
    { value: 'planning', label: 'Planning', color: 'bg-yellow-500' },
    { value: 'inProgress', label: 'In Progress', color: 'bg-blue-500' },
    { value: 'ready', label: 'Ready to Publish', color: 'bg-green-500' }
];

export default function CardModal({ isOpen, onClose, onSubmit, card, defaultStatus = 'ideas' }: CardModalProps) {
    const [isSubmitting, setIsSubmitting] = useState(false);
    const isEditMode = !!card;
    const { pillars, loading: pillarsLoading } = usePillars();

    const {
        register,
        control,
        handleSubmit,
        reset,
        watch,
        setValue,
        formState: { errors }
    } = useForm<CardFormData>({
        resolver: zodResolver(cardFormSchema),
        defaultValues: {
            title: '',
            description: '',
            pillars: [],
            status: defaultStatus,
            dueDate: '',
            progress: undefined
        }
    });

    const { fields: pillarFields, append: appendPillar, remove: removePillar } = useFieldArray({
        control,
        name: 'pillars'
    });

    const watchProgress = watch('progress');
    const watchStatus = watch('status');

    // Reset form when modal opens/closes or card changes
    useEffect(() => {
        if (isOpen) {
            if (card) {
                reset({
                    title: card.title,
                    description: card.description,
                    pillars: card.pillars || [],
                    status: card.status,
                    dueDate: card.dueDate || '',
                    progress: card.progress
                });
            } else {
                reset({
                    title: '',
                    description: '',
                    pillars: [],
                    status: defaultStatus,
                    dueDate: '',
                    progress: undefined
                });
            }
        }
    }, [isOpen, card, defaultStatus, reset]);

    const onFormSubmit = async (data: CardFormData) => {
        console.log('CardModal: Form submission started with data:', data);
        setIsSubmitting(true);
        try {
            // Filter data
            const filteredData = {
                ...data,
                pillars: data.pillars || [],
                dueDate: data.dueDate || undefined,
                progress: data.progress?.label ? data.progress : undefined
            };

            console.log('CardModal: Filtered data for submission:', filteredData);
            console.log('CardModal: Calling onSubmit function...');
            
            const success = await onSubmit(filteredData);
            console.log('CardModal: onSubmit returned:', success);
            
            if (success) {
                console.log('CardModal: Success! Closing modal and resetting form');
                onClose();
                reset();
            } else {
                console.log('CardModal: onSubmit returned false - not closing modal');
            }
        } catch (error) {
            console.error('CardModal: Error submitting form:', error);
        } finally {
            console.log('CardModal: Setting isSubmitting to false');
            setIsSubmitting(false);
        }
    };

    const addPillar = (pillar: Pillar) => {
        appendPillar(pillar);
    };

    const addProgressTracking = () => {
        setValue('progress', { label: '', value: 0, check: false });
    };

    const removeProgressTracking = () => {
        setValue('progress', undefined);
    };

    if (!isOpen) return null;

    return (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
            <div className="bg-slate-800 rounded-lg border border-slate-700 w-full max-w-2xl max-h-[90vh] overflow-y-auto">
                {/* Header */}
                <div className="flex items-center justify-between p-6 border-b border-slate-700">
                    <h2 className="text-xl font-semibold text-white">
                        {isEditMode ? 'Edit Content Card' : 'Create New Content Card'}
                    </h2>
                    <button
                        onClick={onClose}
                        className="text-gray-400 hover:text-white transition-colors"
                        disabled={isSubmitting}
                    >
                        <X className="w-6 h-6" />
                    </button>
                </div>

                {/* Form */}
                <form onSubmit={handleSubmit(onFormSubmit)} className="p-6 space-y-6">
                    {/* Title */}
                    <div>
                        <label className="block text-sm font-medium text-gray-200 mb-2">
                            Title *
                        </label>
                        <input
                            {...register('title')}
                            type="text"
                            className="w-full bg-slate-700 border border-slate-600 rounded-lg px-3 py-2 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                            placeholder="Enter content title..."
                        />
                        {errors.title && (
                            <p className="mt-1 text-sm text-red-400">{errors.title.message}</p>
                        )}
                    </div>

                    {/* Description */}
                    <div>
                        <label className="block text-sm font-medium text-gray-200 mb-2">
                            Description *
                        </label>
                        <textarea
                            {...register('description')}
                            rows={4}
                            className="w-full bg-slate-700 border border-slate-600 rounded-lg px-3 py-2 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent resize-vertical"
                            placeholder="Describe your content idea..."
                        />
                        {errors.description && (
                            <p className="mt-1 text-sm text-red-400">{errors.description.message}</p>
                        )}
                    </div>

                    {/* Status */}
                    <div>
                        <label className="block text-sm font-medium text-gray-200 mb-2">
                            Status *
                        </label>
                        <div className="grid grid-cols-2 md:grid-cols-4 gap-2">
                            {statusOptions.map((option) => (
                                <label
                                    key={option.value}
                                    className={`
                                        flex items-center space-x-2 p-3 rounded-lg border cursor-pointer transition-colors
                                        ${watchStatus === option.value 
                                            ? 'border-blue-500 bg-blue-500/10' 
                                            : 'border-slate-600 hover:border-slate-500'
                                        }
                                    `}
                                >
                                    <input
                                        {...register('status')}
                                        type="radio"
                                        value={option.value}
                                        className="sr-only"
                                    />
                                    <div className={`w-3 h-3 rounded-full ${option.color}`} />
                                    <span className="text-sm text-gray-200">{option.label}</span>
                                </label>
                            ))}
                        </div>
                        {errors.status && (
                            <p className="mt-1 text-sm text-red-400">{errors.status.message}</p>
                        )}
                    </div>

                    {/* Pillars */}
                    <div>
                        <label className="block text-sm font-medium text-gray-200 mb-2">
                            Content Pillars
                        </label>
                        <div className="space-y-3">
                            {pillarFields.length > 0 && (
                                <div className="space-y-2">
                                    {pillarFields.map((field, index) => {
                                        const pillar = field as Pillar;
                                        return (
                                            <div key={field.id} className="flex items-center justify-between p-3 bg-slate-700/50 rounded-lg border border-slate-600">
                                                <div className="flex items-center space-x-3">
                                                    <div className={`w-8 h-8 rounded-lg bg-gradient-to-br ${pillar.color} flex items-center justify-center text-sm`}>
                                                        {pillar.icon}
                                                    </div>
                                                    <span className="text-white font-medium">{pillar.name}</span>
                                                </div>
                                                <button
                                                    type="button"
                                                    onClick={() => removePillar(index)}
                                                    className="p-1 text-red-400 hover:text-red-300 transition-colors"
                                                >
                                                    <Trash2 className="w-4 h-4" />
                                                </button>
                                            </div>
                                        );
                                    })}
                                </div>
                            )}
                            
                            {pillarsLoading ? (
                                <p className="text-sm text-gray-400">Loading pillars...</p>
                            ) : pillars.length === 0 ? (
                                <p className="text-sm text-gray-400">No pillars available. Create some pillars first in the Pillars page.</p>
                            ) : (
                                <div>
                                    <p className="text-sm text-gray-400 mb-2">Select pillars for this content:</p>
                                    <div className="grid grid-cols-1 sm:grid-cols-2 gap-2">
                                        {pillars.filter(pillar => 
                                            !pillarFields.some(field => (field as Pillar).id === pillar.id)
                                        ).map((pillar) => (
                                            <button
                                                key={pillar.id}
                                                type="button"
                                                onClick={() => addPillar(pillar)}
                                                className="flex items-center space-x-2 p-2 bg-slate-700 border border-slate-600 rounded-lg hover:border-blue-500 transition-colors"
                                            >
                                                <div className={`w-6 h-6 rounded bg-gradient-to-br ${pillar.color} flex items-center justify-center text-xs`}>
                                                    {pillar.icon}
                                                </div>
                                                <span className="text-sm text-gray-200">{pillar.name}</span>
                                            </button>
                                        ))}
                                    </div>
                                </div>
                            )}
                            
                            {errors.pillars && (
                                <p className="text-sm text-red-400">{errors.pillars.message}</p>
                            )}
                        </div>
                    </div>

                    {/* Due Date */}
                    <div>
                        <label className="block text-sm font-medium text-gray-200 mb-2">
                            Due Date (Optional)
                        </label>
                        <div className="relative">
                            <input
                                {...register('dueDate')}
                                type="date"
                                className="w-full bg-slate-700 border border-slate-600 rounded-lg px-3 py-2 text-white focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                            />
                            <Calendar className="absolute right-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400 pointer-events-none" />
                        </div>
                        {errors.dueDate && (
                            <p className="mt-1 text-sm text-red-400">{errors.dueDate.message}</p>
                        )}
                    </div>

                    {/* Progress Tracking */}
                    <div>
                        <div className="flex items-center justify-between mb-2">
                            <label className="text-sm font-medium text-gray-200">
                                Progress Tracking (Optional)
                            </label>
                            {!watchProgress ? (
                                <button
                                    type="button"
                                    onClick={addProgressTracking}
                                    className="text-sm text-blue-400 hover:text-blue-300 transition-colors"
                                >
                                    Add Progress
                                </button>
                            ) : (
                                <button
                                    type="button"
                                    onClick={removeProgressTracking}
                                    className="text-sm text-red-400 hover:text-red-300 transition-colors"
                                >
                                    Remove Progress
                                </button>
                            )}
                        </div>
                        
                        {watchProgress && (
                            <div className="space-y-3 p-4 bg-slate-700/50 rounded-lg border border-slate-600">
                                <div>
                                    <label className="block text-sm text-gray-300 mb-1">Progress Label</label>
                                    <input
                                        {...register('progress.label')}
                                        type="text"
                                        placeholder="e.g., Script written, Filming complete"
                                        className="w-full bg-slate-700 border border-slate-600 rounded-lg px-3 py-2 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                                    />
                                </div>
                                <div>
                                    <label className="block text-sm text-gray-300 mb-1">Progress Value (%)</label>
                                    <input
                                        {...register('progress.value', { valueAsNumber: true })}
                                        type="number"
                                        min="0"
                                        max="100"
                                        placeholder="0"
                                        className="w-full bg-slate-700 border border-slate-600 rounded-lg px-3 py-2 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                                    />
                                </div>
                                <div className="flex items-center space-x-2">
                                    <input
                                        {...register('progress.check')}
                                        type="checkbox"
                                        className="w-4 h-4 text-blue-600 bg-slate-700 border-slate-600 rounded focus:ring-blue-500 focus:ring-2"
                                    />
                                    <label className="text-sm text-gray-300">Mark as completed</label>
                                </div>
                                {errors.progress && (
                                    <p className="text-sm text-red-400">{errors.progress.message}</p>
                                )}
                            </div>
                        )}
                    </div>

                    {/* Form Actions */}
                    <div className="flex items-center justify-end space-x-3 pt-6 border-t border-slate-700">
                        <button
                            type="button"
                            onClick={onClose}
                            disabled={isSubmitting}
                            className="px-4 py-2 text-gray-400 hover:text-white transition-colors disabled:opacity-50"
                        >
                            Cancel
                        </button>
                        <button
                            type="submit"
                            disabled={isSubmitting}
                            className="px-6 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg font-medium transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                        >
                            {isSubmitting ? 'Saving...' : isEditMode ? 'Update Card' : 'Create Card'}
                        </button>
                    </div>
                </form>
            </div>
        </div>
    );
}