/**
 * OAuth Status Component
 * Displays current YouTube authentication status
 */

import { useOAuthStore } from '../../store/oauthStore';
import { CheckCircle, AlertCircle, Loader, RefreshCw } from 'lucide-react';
import { useEffect, useState } from 'react';

interface OAuthStatusProps {
  showDetails?: boolean;
  className?: string;
  onClick?: () => void;
}

export default function OAuthStatus({ showDetails = false, className = '', onClick }: OAuthStatusProps) {
  const {
    isAuthenticated,
    status,
    isLoading,
    error,
    isInitialized,
    checkStatus,
    forceRefreshStatus
  } = useOAuthStore();
  
  const [manualRefresh, setManualRefresh] = useState(false);

  // Check status on component mount only (no polling)
  useEffect(() => {
    checkStatus();
  }, [checkStatus]);

  const handleManualRefresh = async () => {
    setManualRefresh(true);
    await forceRefreshStatus();
    setManualRefresh(false);
  };

  if (isLoading || !isInitialized) {
    return (
      <div className={`flex items-center gap-2 ${className}`}>
        <Loader className="w-4 h-4 animate-spin text-blue-500" />
        <span className="text-sm text-gray-600">Checking connection...</span>
      </div>
    );
  }

  if (error) {
    // Determine error type for better messaging
    const isTimeout = error.includes('timeout') || error.includes('timed out');
    const isNetwork = error.includes('network') || error.includes('Failed to fetch');
    
    let displayMessage = 'Connection Error';
    if (isTimeout) {
      displayMessage = 'Connection Timeout';
    } else if (isNetwork) {
      displayMessage = 'Network Error';
    }
    
    return (
      <div className={`${className}`}>
        <div className="flex items-center gap-2">
          <AlertCircle className="w-4 h-4 text-red-500" />
          <span className="text-sm text-red-600">{displayMessage}</span>
          {(isTimeout || isNetwork) && (
            <button
              onClick={handleManualRefresh}
              disabled={manualRefresh}
              className="ml-2 p-1 text-gray-400 hover:text-gray-600 disabled:opacity-50"
              title="Retry connection"
            >
              <RefreshCw className={`w-3 h-3 ${manualRefresh ? 'animate-spin' : ''}`} />
            </button>
          )}
        </div>
        {showDetails && (
          <div className="text-xs text-gray-500 mt-1">
            {error}
          </div>
        )}
      </div>
    );
  }

  // Check if we have token data
  if (isAuthenticated && status) {
    const effectiveStatus = status;
    
    // If we're connected and authenticated, only show expiration warning if refresh explicitly failed
    // Since videos are loading and the connection works, token expiration timestamps are less important
    const shouldShowWarning = false; // Never show expiration warnings if we're connected and authenticated
    
    if (shouldShowWarning) {
      return (
        <div className={`${className}`}>
          <div className="flex items-center gap-2">
            <AlertCircle className="w-4 h-4 text-yellow-500" />
            <span className="text-sm text-yellow-600 font-medium">Token Expired</span>
          </div>
          
          {showDetails && (
            <div className="mt-2 space-y-1">
              <div className="text-xs text-yellow-600 bg-yellow-50 px-2 py-1 rounded">
                 Please refresh your connection
              </div>
              
              {(status?.scopes || effectiveStatus?.scopes) && (
                <div className="text-xs text-gray-500">
                  Previous permissions: YouTube Analytics, Read-only
                </div>
              )}
            </div>
          )}
        </div>
      );
    }
    
    // Token is valid - show clean connected state
    return (
      <div className={`${className}`}>
        <div className="flex items-center gap-2">
          <CheckCircle className="w-4 h-4 text-green-500" />
          <span className="text-sm text-green-600 font-medium">YouTube Connected</span>
        </div>
        
        {showDetails && (
          <div className="mt-2 space-y-1">
            {(status?.scopes || effectiveStatus?.scopes) && (
              <div className="text-xs text-gray-500">
                Permissions: YouTube Analytics, Read-only
              </div>
            )}
          </div>
        )}
      </div>
    );
  }

  return (
    <div className={`flex items-center gap-2 ${className}`} onClick={onClick}>
      <AlertCircle className="w-4 h-4 text-red-500" />
      <span className="text-sm text-red-600">YouTube Not Connected</span>
      {showDetails && (
        <div className="text-xs text-gray-500 mt-1">
          Connect to access detailed analytics
        </div>
      )}
    </div>
  );
}