import { useState, useEffect } from 'react';
import { Pillar } from '@/types/contentStudio';

export function usePillars() {
    const [pillars, setPillars] = useState<Pillar[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    // TEMPORARY FIX: Force use default_user for testing
    const actualUserId = "default_user";

    useEffect(() => {
        fetchPillars();
    }, []);

    const fetchPillars = async () => {
        try {
            setLoading(true);
            const response = await fetch(`/api/pillars/${actualUserId}`);
            
            if (response.ok) {
                const data = await response.json();
                // Transform to Pillar format
                const transformedPillars: Pillar[] = data.map((pillar: any) => ({
                    id: pillar.id,
                    name: pillar.name,
                    icon: pillar.icon,
                    color: pillar.color
                }));
                setPillars(transformedPillars);
                setError(null);
            } else {
                throw new Error('Failed to fetch pillars');
            }
        } catch (error) {
            console.error('Error fetching pillars:', error);
            setError('Failed to load pillars');
            setPillars([]);
        } finally {
            setLoading(false);
        }
    };

    return {
        pillars,
        loading,
        error,
        refetch: fetchPillars
    };
}