import { z } from 'zod';

// Tag validation schema
export const tagSchema = z.object({
    text: z.string().min(1, 'Tag text is required').max(50, 'Tag text must be less than 50 characters'),
    color: z.enum(['blue', 'green', 'purple', 'yellow', 'red', 'orange', 'gray']),
    icon: z.any().optional()
});

// Pillar validation schema
export const pillarSchema = z.object({
    id: z.string().min(1, 'Pillar ID is required'),
    name: z.string().min(1, 'Pillar name is required').max(100, 'Pillar name must be less than 100 characters'),
    icon: z.string().min(1, 'Pillar icon is required'),
    color: z.string().min(1, 'Pillar color is required')
});

// Progress validation schema
export const progressSchema = z.object({
    label: z.string().min(1, 'Progress label is required').max(100, 'Progress label must be less than 100 characters'),
    value: z.number().min(0, 'Progress value must be at least 0').max(100, 'Progress value must be at most 100'),
    check: z.boolean().optional()
});

// Content card form validation schema
export const cardFormSchema = z.object({
    title: z.string()
        .min(1, 'Title is required')
        .max(200, 'Title must be less than 200 characters')
        .trim(),
    description: z.string()
        .min(1, 'Description is required')
        .max(1000, 'Description must be less than 1000 characters')
        .trim(),
    pillars: z.array(pillarSchema)
        .max(5, 'Maximum 5 pillars allowed')
        .optional()
        .default([]),
    status: z.enum(['ideas', 'planning', 'inProgress', 'ready']),
    dueDate: z.string().optional().or(z.literal('')),
    progress: progressSchema.optional()
});

// Quick add card schema (simplified version)
export const quickCardSchema = z.object({
    title: z.string()
        .min(1, 'Title is required')
        .max(200, 'Title must be less than 200 characters')
        .trim(),
    status: z.enum(['ideas', 'planning', 'inProgress', 'ready'])
});

// Search and filter schema
export const filtersSchema = z.object({
    search: z.string().max(100, 'Search term must be less than 100 characters'),
    selectedTags: z.array(z.string()),
    selectedStatuses: z.array(z.enum(['ideas', 'planning', 'inProgress', 'ready'])),
    dateRange: z.object({
        from: z.date(),
        to: z.date()
    }).optional()
});

// AI suggestion to card conversion schema
export const suggestionToCardSchema = z.object({
    suggestionId: z.string().min(1, 'Suggestion ID is required'),
    title: z.string()
        .min(1, 'Title is required')
        .max(200, 'Title must be less than 200 characters')
        .trim(),
    description: z.string()
        .min(1, 'Description is required')
        .max(1000, 'Description must be less than 1000 characters')
        .trim(),
    status: z.enum(['ideas', 'planning', 'inProgress', 'ready']).default('ideas'),
    customTags: z.array(tagSchema).default([])
});

// Export types inferred from schemas
export type CardFormData = z.infer<typeof cardFormSchema>;
export type QuickCardData = z.infer<typeof quickCardSchema>;
export type FiltersData = z.infer<typeof filtersSchema>;
export type SuggestionToCardData = z.infer<typeof suggestionToCardSchema>;
export type TagData = z.infer<typeof tagSchema>;
export type PillarData = z.infer<typeof pillarSchema>;
export type ProgressData = z.infer<typeof progressSchema>;